import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {StorageManagerProvider} from "../../providers/storage-manager/storage-manager";
import {ApiProvider} from "../../providers/api/api";
import {AuthManagerProvider} from "../../providers/auth-manager/auth-manager";
import {GuiManagerProvider} from "../../providers/gui-manager/gui-manager";
import {HomePage} from "../home/home";
import {UserSettingPageModel} from "../../providers/settings/models/user-setting-page.model";
import {UserSettingsAccountInformationPage} from "../user-settings-account-information/user-settings-account-information";
import {UserSettingsRefillKeysPage} from "../user-settings-refill-keys/user-settings-refill-keys";
import {UserSettingsPushNotificationPage} from "../user-settings-push-notification/user-settings-push-notification";
import {UserSettingsCreateAccountPage} from "../user-settings-create-account/user-settings-create-account";
import {UserSettingsFingerprintActivationPage} from "../user-settings-fingerprint-activation/user-settings-fingerprint-activation";

/**
 * Generated class for the UserSettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings',
  templateUrl: 'user-settings.html',
})
export class UserSettingsPage {

  private userSettingsPages: UserSettingPageModel[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private storageManager: StorageManagerProvider,
              private http: ApiProvider, private authManager: AuthManagerProvider, private guiManager: GuiManagerProvider) {

    this.userSettingsPages.push(new UserSettingPageModel("Kontoinformationen", 'UserSettingsAccountInformationPage'));
    this.userSettingsPages.push(new UserSettingPageModel("Passwort ändern", 'UserSettingsChangePasswordPage'));
    this.userSettingsPages.push(new UserSettingPageModel("Biometrisches Login", 'UserSettingsFingerprintActivationPage'));
    this.userSettingsPages.push(new UserSettingPageModel("Nachfüllen von OTP-Codes", 'UserSettingsRefillKeysPage'));
    /*this.userSettingsPages.push(new UserSettingPageModel("Push-Benachrichtigung", 'UserSettingsPushNotificationPage'));*/
    /*this.userSettingsPages.push(new UserSettingPageModel("Message Center", 'UserSettingsMessageCenterPage'));*/

    if (authManager.isMedical && authManager.userGroup == 1) {
      this.userSettingsPages.push(new UserSettingPageModel("Einen Registrierungscode generieren", 'UserSettingsCreateAccountPage'));
    }
    console.log(this.userSettingsPages);
  }

  openHomePage() {
    this.navCtrl.setRoot(HomePage);
  }

  logout() {
    this.authManager.logout();
    this.navCtrl.setRoot(HomePage)
  }

  openDebugger() {
    this.navCtrl.push('DebuggerPage')
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserSettingsPage');
  }

  /**
   * Transitions and opens the page tapped.
   * @param page
   */
  itemTapped(page: UserSettingPageModel) {
    console.log("Item tapped:", page)
    this.navCtrl.push(page.page);
    //this.navCtrl.setRoot(page.page);
  }

}



